  /* ---------------------------------------------------
      SIDEBAR STYLE
  ----------------------------------------------------- */
  
  .wrapper {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-align-items: stretch;
            align-items: stretch;
    -webkit-perspective: 1500px;
            perspective: 1500px;
    background: #f4f7fc;
  }
  
  #sidebar {
    min-width: 250px;
    max-width: 250px;
    background: #4A75E6;
    color: #fff;
    transition: all 0.6s cubic-bezier(0.945, 0.02, 0.27, 0.665);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
  }
  
  #sidebar.active {
    margin-left: -250px;
    -webkit-transform: rotateY(100deg);
            transform: rotateY(100deg);
  }
  
  #sidebar .sidebar-header {
    padding: 20px;
    background: #4A75E6;
  }
  
  #sidebar ul.components {
    padding: 20px 0;
    /* border-bottom: 1px solid #28404d; */
  }
  
  #sidebar ul p {
    color: #fff;
    padding: 10px;
  }
  
  #sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
    color: white;
    text-decoration: none;
  }
 
  #sidebar ul li a:hover {
    color: #4A75E6;
    background: #fff;
  }
  #sidebar ul li .back-home {
    color: black;
  }
  
  #sidebar ul li.active > a,
  a[aria-expanded="true"] {
    color: #fff;
    background: #4A75E6;
  }
  
  a[data-toggle="collapse"] {
    position: relative;
  }
  
  .dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  
  ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #4A75E6;
  }
  
  ul.CTAs {
    padding: 20px;
  }
  
  ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
  }
  
  a.back-home {
    background: #fff;
    color: black;
  }
  
  a.article,
  a.article:hover {
    background: #4A75E6 !important;
    color: #fff !important;
  }
  
  /* ---------------------------------------------------
      CONTENT STYLE
  ----------------------------------------------------- */
  #content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s;
  }
  
  #sidebarCollapse {
    width: 40px;
    height: 40px;
    background: #f5f5f5;
    cursor: pointer;
  }
  
  #sidebarCollapse span {
    width: 80%;
    height: 2px;
    margin: 0 auto;
    display: block;
    background: #555;
    transition: all 0.8s cubic-bezier(0.81, -0.33, 0.345, 1.375);
    transition-delay: 0.2s;
  }
  
  #sidebarCollapse span:first-of-type {
    -webkit-transform: rotate(45deg) translate(2px, 2px);
            transform: rotate(45deg) translate(2px, 2px);
  }
  #sidebarCollapse span:nth-of-type(2) {
    opacity: 0;
  }
  #sidebarCollapse span:last-of-type {
    -webkit-transform: rotate(-45deg) translate(1px, -1px);
            transform: rotate(-45deg) translate(1px, -1px);
  }
  
  #sidebarCollapse.active span {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
    margin: 5px auto;
  }
  
  /* ---------------------------------------------------
      MEDIAQUERIES
  ----------------------------------------------------- */
  @media (max-width: 768px) {
    #sidebar {
      margin-left: -250px;
      -webkit-transform: rotateY(90deg);
              transform: rotateY(90deg);
    }
    #sidebar.active {
      margin-left: 0;
      -webkit-transform: none;
              transform: none;
    }
    #sidebarCollapse span:first-of-type,
    #sidebarCollapse span:nth-of-type(2),
    #sidebarCollapse span:last-of-type {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
      margin: 5px auto;
    }
    #sidebarCollapse.active span {
      margin: 0 auto;
    }
    #sidebarCollapse.active span:first-of-type {
      -webkit-transform: rotate(45deg) translate(2px, 2px);
              transform: rotate(45deg) translate(2px, 2px);
    }
    #sidebarCollapse.active span:nth-of-type(2) {
      opacity: 0;
    }
    #sidebarCollapse.active span:last-of-type {
      -webkit-transform: rotate(-45deg) translate(1px, -1px);
              transform: rotate(-45deg) translate(1px, -1px);
    }
  }
  
  /* Footer */
  
  .section-footer ul li {
    margin-bottom: 5px;
  }
  
  .section-footer ul a {
    color: inherit;
  }
  
  .section-footer ul a:hover {
    color: #4A75E6;
  }
  
  .section-footer .logo-footer {
    height: 40px;
  }
  
  .section-footer .title {
    font-weight: 600;
    margin-bottom: 16px;
    color: #1e2024;
  }
  
  .footer-bottom {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  
  .footer-copyright {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  a.footer-btn {
    color: #007bff;
    font-weight: 500;
}

